import {
  Vue,
  Component,
  Emit,
  Inject,
  Model,
  Prop,
  Provide,
  Watch,
} from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';

/**
 * Component: Card
 */
@Component
export class DtCard extends Vue {
  @Prop({ type: String, default: 'div' })
  public readonly tag!: string;

  private render(h: CreateElement): VNode {
    return h(
      this.tag,
      {
        staticClass: 'dt-card',
      },
      [this.$slots.default],
    );
  }
}
