import {
  Vue,
  Component,
  Emit,
  Inject,
  Model,
  Prop,
  Provide,
  Watch,
} from 'vue-property-decorator';
import { CreateElement, VNode } from 'vue';

export interface NavItem {
  label: string;
  to: string;
  children?: NavItem[];
}

const backgrounds: ReadonlyArray<string> = [
  'home',
  'heroes',
  'store',
  'watch',
  'learn',
  'mods',
];

/**
 * Component: TopBar
 */
@Component
export class DtTopBar extends Vue {
  public readonly itemsSource: NavItem[] = [
    {
      label: '首页',
      to: '/Home',
    },
    {
      label: '数据库',
      to: '/Heroes',
      children: [
        {
          label: '英雄',
          to: '/Heroes',
          children: [
            {
              label: '',
              to: '/Hero',
            },
          ],
        },
        {
          label: '物品',
          to: '/Items',
          children: [
            {
              label: '',
              to: '/Item',
            },
          ],
        },
        {
          label: '机制',
          to: '/Mechanics',
          children: [
            {
              label: '',
              to: '/Mechanic',
            },
          ],
        },
      ],
    },
    {
      label: '电竞',
      to: '/Tournaments',
      children: [
        {
          label: '赛事',
          to: '/Tournaments',
        },
        {
          label: '战队',
          to: '/Teams',
        },
        {
          label: '选手',
          to: '/Player',
        },
      ],
    },
  ];

  public selectedIndex: number = 0;

  public secondary: NavItem[] = [];
  public secondaryHidden: boolean = true;

  public match(navItem: NavItem): boolean {
    return (
      this.$route.path.startsWith(navItem.to) ||
      (!!navItem.children && navItem.children.some(child => this.match(child)))
    );
  }

  @Watch('$route', { immediate: true })
  public onRouteChange(): void {
    this.selectedIndex = this.itemsSource.findIndex(item => this.match(item));
    const navItem: NavItem = this.itemsSource[this.selectedIndex];
    if (navItem && navItem.children && navItem.children.length > 0) {
      this.secondary = navItem.children;
      this.secondaryHidden = false;
    } else {
      this.secondaryHidden = true;
    }
  }

  private render(h: CreateElement): VNode {
    return (
      <div staticClass="dt-top-bar">
        <div
          staticClass="dt-top-bar_secondary"
          class={{ 'is-hidden': this.secondaryHidden }}
        >
          <nav staticClass="dt-top-bar_secondary-nav">
            {this.secondary.map(child => [
              <router-link
                key={child.to + child.label}
                staticClass="dt-top-bar_secondary-item"
                class={{ 'is-selected': this.match(child) }}
                to={child.to}
              >
                {child.label}
              </router-link>,
              <span staticClass="dt-top-bar_secondary-separator dta-separator">/</span>,
            ])}
          </nav>
        </div>
        <div staticClass="dt-top-bar_background-wrapper">
          <div staticClass="dt-top-bar_background is-selected dt-top-bar-background-small topbar_small" />
          {backgrounds.map((bg, index) => (
            <div
              staticClass={`dt-top-bar_background dt-top-bar-background-small topbar_${bg}_small`}
              class={{ 'is-selected': this.selectedIndex === index }}
            />
          ))}
        </div>
        <div staticClass="dt-top-bar_left" />
        <nav staticClass="dt-top-bar_main-nav">
          {this.itemsSource.map((item, index) => (
            <router-link
              key={item.to + item.label}
              staticClass="dt-top-bar_main-item"
              class={{ 'is-selected': this.selectedIndex === index }}
              to={item.to}
            >
              {item.label}
            </router-link>
          ))}
        </nav>
        <div staticClass="dt-top-bar_right">
          <span onClick={async () => this.$loc.select('en')} role="button">
            en
          </span>
          &nbsp;/&nbsp;
          <span onClick={async () => this.$loc.select('zh-CN')} role="button">
            zh-CN
          </span>
        </div>
      </div>
    );
  }
}
