// tslint:disable:no-any no-unsafe-any no-reserved-keywords variable-name

import { default as _Vue, PluginFunction, PluginObject } from 'vue';

let $Vue: typeof _Vue | undefined;
let dict: ReadonlyDictionary<string> = {};

function resources(path: string): string {
  return dict[path] || path;
}

const install: PluginFunction<any> = Vue => {
  if ($Vue && $Vue === Vue) {
    if (process.env.NODE_ENV !== 'production') {
      console.error(
        '[resources] already installed. Vue.use(resources) should be called only once.',
      );
    }

    return;
  }

  Object.defineProperty(Vue.prototype, '$res', {
    get(): (path: string) => string {
      return resources;
    },
  });

  $Vue = Vue;
};

const load: () => Promise<ReadonlyDictionary<string>> = async () =>
  (dict = (await import(/* webpackChunkName: "resources-dict" */ '@src/assets/dict.json'))
    .default as any);

/**
 * Plugin for get production path of resources.
 */
export default {
  install,
  load,
};
