// tslint:disable:no-import-side-effect

/**
 * Root
 */
import 'normalize.css/normalize.css';
import '@src/main.scss';
import Vue from 'vue';
import App from '@src/App';
import router from '@src/router';

import resources from '@src/plugins/resources';
Vue.use(resources);

import database from '@src/plugins/database';
Vue.use(database);

import localization from '@src/plugins/localization';
Vue.use(localization);

Vue.config.productionTip = false;

import * as controls from '@src/controls';
Object.entries(controls).forEach(([name, ctrl]) => Vue.component(name, ctrl));

Promise.all([resources.load(), database.load(), localization.load()])
  .then(() => {
    new Vue({
      router,
      render: h => h(App),
    }).$mount('#app');
  })
  .catch(console.error);
