// tslint:disable:no-reserved-keywords no-single-line-block-comment
import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

/**
 * Router
 */
export default new Router({
  mode: 'history',
  base: 'wiki',
  routes: [
    {
      path: '/Home',
      name: 'Home',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/',
      redirect: '/Home',
    },

    // Database
    {
      path: '/Heroes',
      name: 'Heroes',
      component: async () =>
        import(/* webpackChunkName: "hero-view" */ '@src/views/Heroes'),
    },
    {
      path: '/Hero/:name',
      name: 'Hero',
      component: async () =>
        import(/* webpackChunkName: "hero-view" */ '@src/views/Hero'),
    },
    {
      path: '/Items',
      name: 'Items',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Item/:name',
      name: 'Item',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Mechanics',
      name: 'Mechanics',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Mechanic/:title',
      name: 'Mechanic',
      component: async () => import('@src/views/Home'),
    },

    // E-Sports
    {
      path: '/Tournaments',
      name: 'Tournaments',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Tournament/:id',
      name: 'Tournament',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Teams',
      name: 'Teams',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Team/:id',
      name: 'Team',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Players',
      name: 'Players',
      component: async () => import('@src/views/Home'),
    },
    {
      path: '/Player/:id',
      name: 'Player',
      component: async () => import('@src/views/Home'),
    },
  ],
});
