// tslint:disable:no-any no-unsafe-any no-reserved-keywords variable-name no-invalid-this
import { default as _Vue, PluginFunction, PluginObject } from 'vue';

let $Vue: typeof _Vue | undefined;

type AsyncDict = () => Promise<{ default: ReadonlyDictionary<string> }>;
type AsyncSelectDict = (language?: string) => Promise<ReadonlyDictionary<string>>;

const asyncDictMap: ReadonlyDictionary<AsyncDict> = {
  en: async () => import(/* webpackChunkName: "i18n-en" */ '@i18n/en.json') as any,
  'zh-CN': async () =>
    import(/* webpackChunkName: "i18n-zh-CN" */ '@i18n/zh-CN.json') as any,
};
const langCodes: ReadonlyArray<string> = Object.keys(asyncDictMap).sort();
const dictMap: Dictionary<ReadonlyDictionary<string>> = {};

/**
 * Localization plugin
 */
export class Localization {
  public loading: boolean = false;
  public lang: string = 'auto';
  public dict: ReadonlyDictionary<string> = {};

  public format(key: string): string {
    return this.dict[key] || key;
  }

  public async select(lang: string = 'auto'): Promise<ReadonlyDictionary<string>> {
    if (this.loading) {
      return this.dict;
    }

    let code: string | undefined = lang;
    let asyncDict: AsyncDict;

    if (code === 'auto') {
      if (window && window.navigator) {
        const languages: ReadonlyArray<string> = window.navigator.languages || [
          window.navigator.language,
        ];
        const languagesShort: ReadonlyArray<string> = [
          ...new Set(langCodes.map(lc => lc.split('-')[0])),
        ];

        code = languages.find(l => langCodes.some(lc => lc === l));
        if (code === undefined) {
          for (const short of languagesShort) {
            code = langCodes.find(lc => lc.startsWith(short));
            if (code !== undefined) {
              break;
            }
          }
        }

        if (code === undefined) {
          code = 'en';
        }
      } else {
        code = 'en';
      }
    }

    if (this.lang === code) {
      return this.dict;
    }

    if (dictMap[code]) {
      this.lang = code;
      console.log(`Select language ${code}`);

      return (this.dict = dictMap[code]);
    }

    asyncDict = asyncDictMap[code];
    if (!asyncDict) {
      throw new Error(`No supported language ${lang}`);
    }

    this.loading = true;
    const { default: dict } = await asyncDict();
    this.lang = code;
    this.loading = false;
    console.log(`Select language ${code}`);

    return (this.dict = dictMap[code] = dict);
  }
}

// const localization: Localization = new Localization();
let localization: Localization;
function init(Vue: typeof _Vue): void {
  localization = new Vue({
    data: {
      loading: false,
      lang: 'auto',
      dict: {},
    },
    methods: {
      format: Localization.prototype.format,
      select: Localization.prototype.select,
    },
  });
}

const install: PluginFunction<any> = Vue => {
  if ($Vue && $Vue === Vue) {
    if (process.env.NODE_ENV !== 'production') {
      console.error(
        '[localization] already installed. Vue.use(localization) should be called only once.',
      );
    }

    return;
  }

  init(Vue);

  Object.defineProperty(Vue.prototype, '$loc', {
    get(): Localization {
      return localization;
    },
  });

  $Vue = Vue;
};

const load: AsyncSelectDict = async () => {
  return localization.select('auto');
};

/**
 * Plugin for localization.
 */
export default {
  install,
  load,
};
