// tslint:disable:no-any no-reserved-keywords variable-name

import { default as _Vue, PluginFunction, PluginObject } from 'vue';
import { Ability } from '@db/models/ability';
import { Hero, HeroGroup, Attribute } from '@db/models/hero';
import { Database } from '@db/models/db';

let $Vue: typeof _Vue | undefined;

// tslint:disable-next-line:typedef
let db: Database = {
  // Abilities
  abilityMap: {},
  abilityList: [],
  abilityNames: [],

  // Heroes
  heroNames: [],
  heroMap: {},
  heroList: [],
  heroGroups: [],
};

const install: PluginFunction<any> = Vue => {
  if ($Vue && $Vue === Vue) {
    if (process.env.NODE_ENV !== 'production') {
      console.error(
        '[database] already installed. Vue.use(database) should be called only once.',
      );
    }

    return;
  }

  Object.defineProperty(Vue.prototype, '$db', {
    get(): Database {
      return db;
    },
  });

  $Vue = Vue;
};

const load: () => Promise<Database> = async () => {
  // [{ default: abilityMap }, { default: heroMap }]
  const modules: [
    { default: Dictionary<Ability> },
    { default: Dictionary<Hero> }
  ] = (await Promise.all([
    import(/* webpackChunkName: "data-ability" */ '@db/npc_abilities'),
    import(/* webpackChunkName: "data-hero" */ '@db/npc_heroes'),
  ])) as any;

  console.log(modules[0].default);
  console.log(modules[1].default);

  const { default: abilityMap } = modules[0];
  const abilityList: Ability[] = Object.values(abilityMap).sort((a, b) => a.id - b.id);
  const abilityNames: string[] = abilityList.map(a => a.name);

  const { default: heroMap } = modules[1];
  const heroList: Hero[] = Object.values(heroMap).sort((a, b) => a.id - b.id);
  const heroNames: string[] = heroList.map(h => h.name);
  const heroGroups: HeroGroup[] = [
    Attribute.strength,
    Attribute.agility,
    Attribute.intelligence,
  ].map(primary => ({
    primary: Attribute[primary],
    heroes: heroList.filter(h => h.attributes.primary === primary),
  }));

  return (db = {
    // Abilities
    abilityMap,
    abilityList,
    abilityNames,

    // Heroes
    heroMap,
    heroList,
    heroNames,
    heroGroups,
  });
};

/**
 * Plugin for game data
 */
export default {
  install,
  load,
};
